*,html {
  font-family: 'Roboto', sans-serif;
  background-color: #E8E8E8;
  margin: 0;
  padding: 0; }

.screen {
  margin: 4vh;
  height: 90vh;
  width: 77.5vh;
  background-color: white;
  border-radius: 10px;
  -webkit-box-shadow: 1px 24px 24px -11px rgba(0,0,0,0.35);
  -moz-box-shadow: 1px 24px 24px -11px rgba(0,0,0,0.35);
  box-shadow: 1px 24px 24px -11px rgba(0,0,0,0.35); }
